import React, { useEffect, useState } from 'react';
import Header from '../Header';
import getUsers from '../../controller/users/get-total';
import deleteUser from '../../controller/users/delete';
import List from './user-list';
import Add from './add';

const Admin = (props) => {
  const [user, setUser] = useState([]);
  const [show, setShow] = useState(false)
  useEffect(() => {
    getUsers(localStorage.getItem('token'))
      .then(data => setUser(data))
  })

  return (
    <main className='w-100'>
      <Header logoutprop={props} />
      <h3>Lista de trabajadores</h3>
      <div >
        {user !== 0 && user.map((el) =>
          <div key={el._id} className="col-md-6 d-flex">
            <div className="col-2">
              <button data-testid='deleteItem' onClick={() => deleteUser(localStorage.getItem('token'), el._id)} className='border-none transparent padding-trash'><i className="fas fa-edit text-gray"></i></button>
              <button data-testid='deleteItem' onClick={() => deleteUser(localStorage.getItem('token'), el._id)} className='border-none transparent padding-trash'><i className="fas fa-trash-alt text-gray"></i></button>
            </div>

            <List user={el} />

          </div>
        )}
      </div>
      <button data-testid="Agregar" onClick={() => setShow(true)}>Agregar</button>
      <section>
        {show && <Add />}
      </section>
    </main>
  )
}

export default Admin;